// Use this file to overwrite the basic Bootstrap variables and add your own variables
// To overwrite a Bootstrap variable you dont have to touch the Bootstrap folder.
// Just copy a variable from src/sass/assets/bootstrap5/_variables.scss, paste it here and edit the value.


@import "props";

$primary: $col-primary;
$secondary: $col-secondary;

@import "../assets/bootstrap5/functions";
@import "../assets/bootstrap5/variables";
@import "../assets/bootstrap5/maps";
@import "../assets/bootstrap5/utilities";

$utilities: map-merge( 
$utilities, ( "width": map-merge( 
    map-get( $utilities, "width"), ( responsive: true ),),)
);

$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1320px
);

$container-max-widths: (
    sm: 540px,
    md: 720px,
    lg: 960px,
    xl: 1260px,
);

